/* MAIN LAYOUT */

.layout {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.4fr 3.4fr 0.2fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "mainHeader"
      "MainViewRouting"
      "mainFooter";
  }
  
  .mainHeader { grid-area: mainHeader; }
  
  .MainViewRouting { grid-area: MainViewRouting; }
  
  .mainFooter { grid-area: mainFooter;}


/* HEADER */

.mainHeader {
    display: grid;
    grid-template-columns: 0.8fr 4fr 1fr 1.1fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "headerLogo headerNavigation headerCart headerClient";
      position: -webkit-sticky;
      position: sticky;
      
    top: 0px;
    background-color: white;
    border-bottom: 1px solid  rgb(229, 229, 229);
  }
  
  .headerLogo { grid-area: headerLogo; }
  
  .headerNavigation { grid-area: headerNavigation; }
  
  .headerCart { grid-area: headerCart; }
  
  .headerClient { grid-area: headerClient; }
  

/* FOOTER */  

.mainFooter {
    border-top: 1px solid rgb(229, 229, 229);
    background: white; 
    box-shadow: 0px -7px 19px 0px #B5B5B5;
  }




  

  

/* ADMIN */

.adminView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "adminNavigation adminRouting";
}
  
  
.adminNavigation {
     grid-area: adminNavigation; 
}
  
.adminRouting { 
      grid-area: adminRouting;
}

  
/* COMPANY */

.companyView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "companyNavigation companyRouting";
}
  
  
.companyNavigation { 
      grid-area: companyNavigation;
}
  
.companyRouting { 
    grid-area: companyRouting;
}
  


/* CUSTOMER */
  
.customerView {
    height: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1.7fr;
    grid-template-rows: 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      "customerNavigation customerRouting";
}
  
  
.customerNavigation {
   grid-area: customerNavigation;

}
  
  .customerRouting { 
    grid-area: customerRouting;
}

